import type { Agent, AgentRole, AgentStatus } from '@/types/api'

let seq = 1
const id = () => `agent-${seq++}`
const now = () => new Date().toISOString()

const ROLE_NAMES: Record<AgentRole, string> = {
  coordinator: 'åè°ƒè€…',
  requirements: 'éœ€æ±‚åˆ†æå¸ˆ',
  architecture: 'æ¶æ„å¸ˆ',
  frontend: 'å‰ç«¯å·¥ç¨‹å¸ˆ',
  backend: 'åç«¯å·¥ç¨‹å¸ˆ',
  testing: 'æµ‹è¯•å·¥ç¨‹å¸ˆ',
  devops: 'DevOps å·¥ç¨‹å¸ˆ',
  review: 'ä»£ç å®¡æŸ¥å‘˜',
}

export function makeAgent(overrides: Partial<Agent> = {}): Agent {
  const role: AgentRole = overrides.role ?? 'frontend'
  return {
    id: id(),
    name: ROLE_NAMES[role],
    role,
    status: 'idle',
    workspaceId: 'ws-default',
    model: 'claude-sonnet-4-6',
    tools: [],
    createdAt: now(),
    updatedAt: now(),
    ...overrides,
  }
}

export function makeAgentTeam(workspaceId: string): Agent[] {
  const roles: AgentRole[] = [
    'coordinator',
    'requirements',
    'architecture',
    'frontend',
    'backend',
    'testing',
    'devops',
    'review',
  ]
  const statuses: AgentStatus[] = ['running', 'idle', 'idle', 'running', 'idle', 'idle', 'idle', 'idle']
  return roles.map((role, i) =>
    makeAgent({ role, workspaceId, status: statuses[i] ?? 'idle' }),
  )
}
